import VueRouter from 'vue-router';
// Pages
import VendingMachine from '@/template/components/VendingMachine/VendingMachine.vue';
var routes = [
    {
        path: '*',
        redirect: { name: 'home' }
    },
    {
        name: 'home',
        path: "/",
        component: VendingMachine
    },
];
export var router = new VueRouter({
    mode: 'history',
    routes: routes,
    linkActiveClass: 'is-active'
});
